import React from 'react';
import $ from 'jquery';
export function filesDownload(json) {
  const parts = window.location.href.split('/');
  const id = parts[parts.length - 1];

  $.ajax({
    url: '/web/ajax/save-json',
    data: { 'id': id, 'json': JSON.stringify(json)},
    type: 'POST',
    success: function(data) {
      window.history.back();
    }
  });

}
